.skills-section {
    text-align: center;
    padding: 100px 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
}

.skills-title {
    font-size: 32px;
    color: #ffffff;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px; /* Расстояние между линиями и текстом */
    position: relative;
    width: 100%; /* Убедимся, что контент центрируется */
    margin-top: 65px;
}

/* Контейнер с горизонтальным скроллом */
.skills-wrapper {
    position: relative;
    width: 80%;
    overflow-x: auto;
    display: flex;
    justify-content: center;
    padding: 80px 0;
}

/* Контейнер с колонками */
.skills-container {
    display: flex;
    gap: 440px;
    white-space: nowrap;
}

/* Колонка */
.skill-category {
    text-align: center;
    min-width: 250px;
}

/* Заголовки категорий */
.skill-category h3 {
    font-size: 22px;
    color: #ffcc00;
    margin-bottom: 20px;
}

/* Список навыков */
.skill-list {
    list-style: none;
    padding: 0;
}

.skill-item {
    font-size: 18px;
    color: white;
    background: #333;
    padding: 10px 20px;
    border-radius: 8px;
    margin-bottom: 8px;
    cursor: pointer;
    transition: transform 0.3s, background 0.3s;
}

.skill-item:hover {
    transform: scale(1.1);
    background: rgba(255, 204, 0, 0.5);
}

/* Скролл */
.skills-wrapper::-webkit-scrollbar {
    height: 8px;
}

.skills-wrapper::-webkit-scrollbar-thumb {
    background-color: rgba(255, 204, 0, 0.6);
    border-radius: 10px;
}

.skills-wrapper::-webkit-scrollbar-track {
    background: #222;
}




/* Создаем линии слева и справа */
.skills-title::before,
.skills-title::after {
    content: "";
    flex-grow: 0; /* Позволяет линиям адаптироваться */
    height: 2px; /* Толщина линии */
    background-color: #ffffff; /* Цвет линии */
}

/* Изменяем длину линий */
.skills-title::before {
    width: 100px; /* Длина левой линии */
}

.skills-title::after {
    width: 100px; /* Длина правой линии */
}


@media (max-width: 768px) {
    /* 🔹 Делаем секцию видимой и корректируем отступы */
    .skills-section {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        padding: 40px 10px;
        width: 100%;
        max-width: 100%;
        opacity: 1 !important; /* Убедимся, что секция не скрыта */
        visibility: visible !important;
    }

    /* 🔹 Заголовок секции */
    .skills-title {
        font-size: 22px;
        margin-bottom: 20px;
    }

    /* 🔹 Контейнер с колонками навыков (2 в ряд) */
    .skills-container {
        display: grid;
        grid-template-columns: repeat(2, minmax(140px, 1fr)); /* Гибкие две колонки */
        gap: 15px;
        width: 100%;
        max-width: 90%;
        justify-content: center;
    }

    /* 🔹 Каждая категория навыков */
    .skill-category {
        width: 100%;
        text-align: center;
        background: rgba(255, 255, 255, 0.05);
        padding: 12px;
        border-radius: 8px;
        opacity: 1 !important; /* Делаем видимыми */
        visibility: visible !important;
    }

    /* 🔹 Заголовки категорий */
    .skill-category h3 {
        font-size: 16px;
        margin-bottom: 6px;
    }

    /* 🔹 Список навыков адаптируем */
    .skill-list {
        padding: 0;
        list-style: none;
        display: flex;
        flex-direction: column;
        gap: 5px;
        align-items: center;
    }

    /* 🔹 Оформление каждого навыка */
    .skill-item {
        font-size: 14px;
        padding: 6px 12px;
        border-radius: 6px;
        background: rgba(255, 255, 255, 0.1);
        transition: transform 0.3s, background 0.3s;
        width: 100%;
        text-align: center;
    }

    /* 🔹 Эффект при наведении */
    .skill-item:hover {
        background: rgba(255, 204, 0, 0.5);
        transform: scale(1.05);
    }

    /* 🔹 Убираем скрытие элементов */
    .skills-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        opacity: 1 !important;
        visibility: visible !important;
    }

    .skills-wrapper::-webkit-scrollbar {
        display: none;
    }
}





